.profil {
    background-color: #12002B;
    padding: 20px 35px 35px 35px;
    color: white;
}

.profil-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 50px;

    h2 {
        text-align: center;
    }

    p {
        font-size: 25px;
        margin-bottom: 10px;
    }

    button {
        margin-top: 20px;
        background-color: #00bc77;
        height: 45px;
        width: 120px;
        font-size: 16px;
        color: #FFF;
        font-weight: 700;
        border-bottom: 2px solid #006741;
        border-right: 2px solid #006741;
        border-top: 2px solid #00bc77;
        border-left: 2px solid #00bc77;
        cursor: pointer;
    }

    input {
        margin-top: 10px;
        height: 45px;
        width: 300px;
        font-size: 16px;
        color: #000;
        font-weight: 700; 
    }

    label {
        margin-top: 10px;
        font-size: 16px;
        color: #FFF;
        font-weight: 700;
    }

    .profil-buttons {
        margin-top: 25px;
        
        button {
            margin-right: 15px;
            margin-left: 15px;
        }
    }
}

@media (min-width: 1024px) {
    .profil {
        padding: 20px 100px 50px 100px;
    }
}
